import { db } from './firebase-config'
import "firebase/firestore";
import "firebase/auth";
import { useEffect, useState } from "react";
import { Button } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { AgGridColumn, AgGridReact } from 'ag-grid-react';

import 'ag-grid-enterprise';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';

const App = () => {
  const [gridApi, setGridApi] = useState(null);
  const [gridColumnApi, setGridColumnApi] = useState(null);
  const [rowsVal, setRowsVal] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [values, setValues] = useState({
    id: "",
    name: "",
    case: "",
    comments: ""
  });
  const columnDef = [
    { field: 'id', headerName: 'ID', type: 'rightAligned' },
    { field: 'name', headerName: 'Name' },
    { field: 'case', headerName: 'Case' },
    { field: 'comments', headerName: 'Comments' },    
  ];

  useEffect(() => {
    setValues(val => ({ ...val, id: rowsVal.length + 1 }));
  }, [rowsVal]);

  useEffect(() => {
    db.collection("users")
      .orderBy('id')
      .onSnapshot(querySnapshot => {
        const data = querySnapshot.docs.map(doc => doc.data());
        setRowsVal(data);
      });
  }, []);

  const handleCreateRecord = () => {
    setIsOpen(false);
    db.collection("users").add({
      id: values.id,
      name: values.name,
      case: values.case,
      comments: values.comments,
      datetime: new Date(),
    })
      .then(() => {
        setValues({});
      })
      .catch((error) => {
        console.error("Error adding document: ", error);
      });
  };

  const handleOpen = () => {
    setIsOpen(true);
  };

  const handleExport = () => {
    var params = {
      fileName: "All Records",
      sheetName: "Record Page 1",
      allColumns: true,
    }
    gridApi.exportDataAsExcel(params);
  };

  const handleClose = () => {
    setIsOpen(false);
  };

  const handleChange = name => event => {
    setValues({ ...values, [name]: event.target.value });
  }

  const onGridReady = params => {
    setGridApi(params.api);
    setGridColumnApi(params.columnApi);
  }

  return (
    <div className="ag-theme-alpine" style={{ height: 500, width: '100%' }}>
      <AgGridReact
        onGridReady={onGridReady}
        rowData={rowsVal}
        columnDefs={columnDef}>
      </AgGridReact>
      <Button variant="contained" color="primary" onClick={handleOpen}>
        Create
      </Button>
      <Button variant="contained" color="primary" onClick={handleExport}>
        Export
      </Button>
      <Dialog open={isOpen} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Add Record</DialogTitle>
          <div
            style={{ display: 'flex', flexDirection: 'column', margin:'20px'}}>
            <TextField
              id="id"
              label="ID"
              value={values.id}
              style={{margin:'15px'}}
              onInput={handleChange('id')} />
            <TextField
              id="Name"
              label="Name"
              value={values.name}
              style={{margin:'15px'}}
              onInput={handleChange('name')}
            />
            <TextField
              id="Case"
              label="Case"
              value={values.case}
              style={{margin:'15px'}}
              onInput={handleChange('case')}
            />
            <TextField
              id="Comments"
              label="Comments"
              style={{margin:'15px'}} 
              value={values.comments}
              multiline
              rows={5}
              rowsMax={10}
              onInput={handleChange('comments')}
            />
          </div>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleCreateRecord} color="primary" disabled={Object.keys(values).some((val) => values[val] === '')}>
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default App;